/* comment */

.sp1_task_comments{
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}


.sp1_task_write_comment{
    display: flex; 
    gap: 10px;
}

.sp1_task_comment_send_box{ 
    display: flex;
    & .__avatar{
        width: 48px;
        height: 48px;
        display: inline-block;

    }

    & .__send-box{
        flex: 1;
        max-width: 700px;
    }
}

.sp1_task_comments{
    width: 100%; 
}


.sp1_task_comment_actions{
    margin-top: 6px;
    display: flex;
    align-items: center;
    gap: 6px;
    color: #777;

    a{
        color: #777;
        font-size: 14px;
        
        &:hover{
            text-decoration: underline;
            color:#333 !important;
        }
    }
}


.sp1_task_comment_replies{
    width: 100%;
    max-width: 650px;
}



.sp1_task_card--sub-card-modal{
    display: flex;
    align-items: center;
    justify-content: center;
}

.sp1_task_card--sub-card-modal-content{
    width: 100%;
    max-width: 650px;
    background: #fff;
    margin: auto; 
    overflow: hidden;
    border-radius: 10px; 

    & .__header{
        background-color: #ececec;
        padding: 6px 10px;
        margin-bottom: 10px;
        border: 1px solid #E7EFFC;
        button{
            margin-left: auto;
            font-size: 18px;
            margin-right: 10px;
            width: 28px;
            height: 28px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 6px;
            color: #5c5c5c;

            &:hover{
                background-color:#f3f3f3;
                color: #525252; 
            }
        }
    }

    & .__content{
        height: 500px;
        overflow-x: hidden;
        overflow-y: auto;
        padding: 16px;
    }

    & .__footer{
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        border-top: 1px dashed #eeeeee;
        box-shadow: 0 -2px 6px rgba(0,0,0,.1);

        button{
            background-color: #c4e8ff;
        }
    }
}


